import * as React from "react";
import { graphql, PageProps } from "gatsby";
import Img from "gatsby-image";
import { GoogleFormProvider, useGoogleForm } from "react-google-forms-hooks";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import form from "../form.json";
import ShortAnswerInput from "../components/ShortAnswerInput";
import LongAnswerInput from "../components/LongAnswer";
import CheckboxInput from "../components/CheckBoxInput";
import { SEO } from "../components/SEO";
import ContactForm from "../components/ContactForm";

const ContactPage: React.FC<PageProps> = (props) => {
  const data = props.data as any;
  const backgroundImage = data.backgroundImage;

  const methods = useGoogleForm({ form: form as any });

  const onSubmit = async (data: any) => {
    console.log(">>> Here is the data", data);
    await methods.submitToGoogleForms(data);
    window.location.href = "/thank-you";
  };

  return (
    <main>
      <div className="relative">
        <Img
          fluid={backgroundImage.childImageSharp.fluid}
          alt={backgroundImage.name}
          className="!absolute z-[-1] w-full h-full !object-cover !object-center"
        />
        <div className="blur-background">
          <Navbar />
        </div>
        <ContactForm />
      </div>
      <Footer />
    </main>
  );
};

export default ContactPage;

export const Head = () => <SEO title={`Contact Us | VPTronics`} />;

export const query = graphql`
  query {
    backgroundImage: file(relativePath: { eq: "solar-farm.jpg" }) {
      name
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`;
